/* eslint-disable */
import { defineStore, storeToRefs } from "pinia"
import { useInitUnit } from '@/store/InitUnit'

export const useSpine = defineStore('spine', {
    state: () => {
        const { unit } = storeToRefs(useInitUnit())
        return {
            unit:unit,
            isSpine:false,
            players:[],
        }
    },
    actions: {
        setSpine(id, skin) {
            console.log(id, skin)
            const node = document.getElementById(id);
            console.log(node)
            node.innerHTML = '';
            console.log(node)
            this.player = new spine.SpinePlayer(id, {
                jsonUrl: `spine/${skin}.json`,
                atlasUrl: `spine/${skin}.atlas`,
                backgroundColor: "#00000000",
                defaultMix: 0.4,
                animation:"idle",
                showControls: false,
                premultipliedAlpha: true,
                alpha: true,
                viewport: {
                    debugRender: false,
                    transitionTime: 1,
                    // x: -750, //big:left, small:right
                    // y: -50, //big:down, small:up
                    // width: 750,
                    // height: 750 // -800, 0, 1600, 1600
                    // padTop: "100%",
                    // padBottom: "100%",
                    // padRight: "5%",
                    // padLeft: "5%"
                }
            })
        },
        setSpine_SD() {
            console.log(this.unit.SD.slice().reverse())
            this.unit.SD.slice().reverse().forEach((item, index) => {
                console.log(item)
                if (item != null){
                    this.setSpine(`spine_SD_${this.unit.SD.length - index - 1}`, item)
                    this.players.push(this.player)
                }
            });
        },
        squad_SD(id) {
            console.log(id)
            this.setSpine("drag_" + id, id)
            this.players.push(this.player)
        },
        changeAnimation(ani) {
            this.players.forEach((item, index) => {
                item.animationState.setAnimation(0, ani, true, 0)
                item.animationState.addAnimation(0, 'idle', true, 3)
            })
        },
        storyUse(skin, id) {
            const node = document.getElementById(id);
            if (skin == "") {
                node.innerHTML = '';
            }else{
                node.innerHTML = '';
                this.player = new spine.SpinePlayer(id, {
                    jsonUrl: `spine/${skin}.json`,
                    atlasUrl: `spine/${skin}.atlas`,
                    backgroundColor: "#00000000",
                    showControls: false,
                    premultipliedAlpha: true,
                    alpha: true,
                    viewport: {
                        debugRender: false,
                    }
                })
            }
        }
    },
})