<template>
    <div class="container">
        <AbyssTower/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useWeapon } from '@/store/Weapon'
import { useLanguage } from '@/store/Language'
import AbyssTower from '@/components/AbyssTower.vue'
import { storeToRefs } from 'pinia'

export default {
    name: 'EquipmentView',
    components: {
        AbyssTower,
    },
    setup() {
        const { language } = storeToRefs(useLanguage())
        useLanguage().changeLanguage()
        watch(language, (nV) => {
            useLanguage().changeLanguage()
            useWeapon().changeLanguage(nV)
        })
    }
}
</script>

