<template>
    <div class="row justify-content-center">
        <div class="col-md-8 offset-md-auto col-10">
            <table class="table table-bordered">
                <tbody>
                    <tr>
                        <td><div id="div0" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div1" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div2" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div3" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                    </tr>
                    <tr>
                        <td><div id="div4" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div5" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div6" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div7" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                    </tr>
                    <tr>
                        <td><div id="div8" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div9" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div10" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                        <td><div id="div11" class="squad" ondrop="drop(event)" ondragover="allowDrop(event)"></div></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <div>
        <div class="col-4 col-md-2 p-0 mb-1" style="display: inline-block;" v-for="unit in unitsFilter" :key="unit.Name[language]">
            <div class="card text-center h-100" style="position: relative; display: flex;">
                <img :id="unit.SD[0]" @mousedown="toSD" draggable="true" ondragstart="drag(event)" :src="`img/UI_Card/${unit.Imgs[0]}`" alt="">
                <div :id="`drag_${unit.SD[0]}`" class="dragTarget" draggable="true" ondragstart="drag(event)"></div>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch, onUpdated, onMounted } from 'vue'
import { computed } from '@vue/runtime-core'
import { useMobile } from "@/store/Mobile"
import { useInitUnit } from "@/store/InitUnit"
import { useLanguage } from '@/store/Language'
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'
import Characters from '@/assets/json/Characters.json'

export default {
    name: 'AbyssTower',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language } = storeToRefs(useLanguage())
        const { isSpine } = storeToRefs(useSpine())

        const setSpine = (id) => {
            useSpine().setSpine(id, unit.value.Fullbody[0])
            isSpine.value = !isSpine.value
        }

        // onMounted(() => {
        //     useSpine().setSpine_SD()
        // })
        // onUpdated(() => {
        //     useSpine().setSpine_SD()
        // })

        const {
          unit,
          searchName,
          searchSkill,
          elementBtns,
          classBtns,
          elementList,
          classList,
          sortList,
        } = storeToRefs(useInitUnit())

        $(document).ready(function() {
            $(this).on("click", ".setBtn", function() {
                $(this).children().toggle();
            });
            $(this).on("click", "#filterBtn4", function() {
                $(this).toggle();
                $(this).siblings("#filterBtn5").toggle();
            });
            $(this).on("click", "#filterBtn5", function() {
                $(this).toggle();
                $(this).siblings("#filterBtn6").toggle();
            });
            $(this).on("click", "#filterBtn6", function() {
                $(this).toggle();
                $(this).siblings("#filterBtn4").toggle();
            });
        });
        elementBtns.value = [false, false, false, false, false]
        classBtns.value = [false, false, false, false, false, false]
        searchName.value = ''
        searchSkill.value = ''

        const sortValue = ref('Update')
        const sortFilter = (a, b) => {
            sortValue.value = sortList.value[(sortList.value.indexOf(sortValue.value) + 1) % sortList.value.length]
        }
        const sortCharacters = (a, b) => {
            if (sortValue.value == 'ATK') {
                return b.ATK - a.ATK
            }else if (sortValue.value == 'DEF') {
                return b.DEF - a.DEF
            }else if (sortValue.value == 'HP') {
                return b.HP - a.HP
            }else if (sortValue.value == 'Name') {
                return (a.Name[language.value] < b.Name[language.value]) ? -1 : (a.Name[language.value] > b.Name[language.value]) ? 1 : 0
            }else if (sortValue.value == 'Update') {
                return (a.Cname > b.Cname) ? -1 : (a.Cname < b.Cname) ? 1 : 0
            }
        }

        const unitsFilter = computed(() => {
            return Characters
                .sort((a, b) =>{ return sortCharacters(a, b)})
                .filter((unit) => {
                var _element = (elementBtns.value[elementList.value.indexOf(unit.Element[0])]) || (!elementBtns.value.includes(true))
                var _class = (classBtns.value[classList.value.indexOf(unit.Class[0])]) || (!classBtns.value.includes(true))
                var _name = unit.Name[language.value].toLowerCase().includes(searchName.value.toLowerCase())
                if (unit.ActiveDescription.length > 0 && unit.PassiveDescription.length > 0) {
                    var _active = unit.ActiveDescription[language.value][0].toLowerCase().includes(searchSkill.value.toLowerCase())
                    var _passive = unit.PassiveDescription[language.value][0].toLowerCase().includes(searchSkill.value.toLowerCase())
                    var _traits = false
                    unit.TraitsDescription.forEach((item) => {
                        _traits = _traits || item[language.value].toLowerCase().includes(searchSkill.value.toLowerCase())
                    })
                }else {
                    var _active = unit.Name[language.value].toLowerCase().includes(searchSkill.value.toLowerCase())
                    var _passive = unit.Name[language.value].toLowerCase().includes(searchSkill.value.toLowerCase())
                    var _traits = unit.Name[language.value].toLowerCase().includes(searchSkill.value.toLowerCase())
                }
                return (_element && _class) && _name && (_active || _passive || _traits) && sortValue
                
            })
        })

        function toSD(event) {
            console.log(event.target.id)
            useSpine().squad_SD(event.target.id)
        }

        return {
            language,
            isMobile,
            isSpine,
            unit,
            unitsFilter,
            toSD
            // setSpine,
            // allowDrop,
            // drag,
            // drop
        }
    }
}
</script>

<style scoped>
table {
    table-layout:fixed;
    text-align: center;
    transform: rotateY(180deg) skewX(45deg);
    background-color: rgba(255, 255, 255, 0.5)
}
td {
  position: relative;
    opacity: 100%;
}

td > div {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

td:before {
  content: "";
  display: block;
  padding-top: 33%;
}
.squad {
    transform: skewX(-45deg);
}

.dragTarget {
    float: left;
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 150%;
    z-index: -1000;
    /* padding: 5px; */
}
</style>
